/* ---------------------------- */
div.consensus {
  width: 68px;
  height: 68px;
  display: inline-block;
  transition: transform 0.2s;
  transform: scale(1);
}
div.consensus div.outer_ring {
  display: inline-block;
  width: 68px;
  height: 68px;
  border-radius: 50%;
  padding: 4px;
  background-color: #081c22;
}
div.consensus .user_score_chart {
  position: relative;
  display: inline-block;
  width: 100%;
  height: 100%;
  text-align: center;
}
div.consensus .user_score_chart .percent {
  width: 100%;
  height: 100%;
  z-index: 2;
  display: flex;
  align-items: center;
  justify-content: center;
}
div.consensus .user_score_chart .percent .icon {
  color: #fff;
}
div.consensus .user_score_chart canvas {
  background-color: transparent;
  position: absolute;
  top: 0;
  left: 0;
}
/* ------------------ */
.d {
  display: inline-flex;
}

.main-container {
  background-repeat: no-repeat;
  min-height: 95vh;
  width: 100vw;
  background-size: cover;
}

.font {
  color: white;
  font-size: 1.2rem;
}
.image-container {
  position: relative;
  overflow: hidden;
  display: flex;
  align-items: center;
}
.overlay::before {
  content: "";
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  border-radius: 1rem;
  position: absolute;
  transition: all 0.5s ease-in-out;
}

.overlay:hover::before {
  background-color: rgba(0, 0, 0, 0.9);
}
.image-container:hover .mainTitle {
  transform: translate(0, 0);
}
.mainTitle {
  position: absolute;
  bottom: 0;
  right: 0;
  transition: all 1s ease-in-out;
  padding: 0.75rem;
  color: white;
  font-size: 2rem;
  transform: translate(100%, 100%);
}
