/* ipad and ipad pro */
@media only screen and (min-width: 768px) and (max-width: 1024px){
.mainTitle {
    font-size: 1rem;
    top: 0;
    left: 0;
    transform: translate(-100%, -100%);
  }
}
/* Mobiles */
@media only screen and (min-width: 200px) and (max-width: 767px) {
  .mainTitle {
    font-size: 4rem;
    top: 0;
    left: 0;
    transform: translate(-100%, -100%);
  }

  .con{
    display: flex;
    flex-direction: column;
  }
  .PopularMoviesTitle{
     text-align: center;
     font-style: normal;
     margin-top: 1rem;
  }

  .btn_container{
    margin-top: 1rem;
  }
  
}

